p {
  font-size: 22px;
  line-height: 140%;
  font-weight: 100;
}

.wrapper {
  width: 546px;
  color: #c0baba;
  margin: 0 auto;
}

.inner {
  padding: 40px 20px;
  position: relative;
}

.title {
  text-align: center;
  font-weight: 100;
  margin-bottom: 25px;
  font-size: 50px;
}

.subject {
  font-weight: 100;
  margin-bottom: 17px;
  font-size: 35px;
}

.textWrapper {
  padding-left: 25px;
}

.text {
  margin-bottom: 10px;
}

.tickedText {
  display: flex;
  align-items: center;
}

.icon {
  padding: 0 15px 0 10px;
  fill: var(--primary-green-color);
}

.image {
  position: absolute;
  top: -8px;
  right: -126px;
  rotate: 5deg;
  z-index: -1;
  opacity: 0.4;
  border-radius: 48px;
  box-shadow: -1px 1px 15px var(--primary-yellow-color);
}
